<template>
	<view class="content">
		<!-- 搜索框 -->
		<view class="search-area">
			<u-search v-model="params.keywords" @custom="search" @search="search" :shape="'round'" @clear="clear" 
				:clearabled="false" :show-action="false" :input-align="'left'" placeholder="最新资讯"></u-search>
		</view>
		
		<!-- 类别筛选 -->
		<view class="u-tabs-box">
			<u-tabs :list="categoryList" keyName="dictLabel" @click="changeCategory" :current="categoryCurrent"></u-tabs>
		</view>
		
		<!-- 幻灯片栏 -->
		<view class="swiper-box">
			<u-swiper :list="bannerList" keyName="bannerUrl" :imgDomain="imgDomain" indicator indicatorMode="line" circular></u-swiper>
		</view>

		<view class="new-list" v-if="newsList.length > 0">
			<view class="item" v-for="(item, index) in newsList" :key="index">
				<view class="info" @click="clickItem(item)">
					<view class="text">
						<view class="title">{{ item.newsTitle }}</view>
						<view class="other">
							<view class="left">
								<view class="type">{{ item.newsCategoryName }}</view>
								<view class="time">
									<uni-dateformat :date="item.createTime" :format="'yyyy-MM-dd hh:mm'"></uni-dateformat>
								</view>
							</view>
							<view class="right view">
								<image src="/static/img/common/icon_view.png"></image>
								<text>{{ item.newsViews }}</text>
							</view>
						</view>
					</view>
					<view class="photo"><image :src="imgDomain + item.newsImg.split(',')[0]" mode="aspectFill"></image></view>
				</view>
				<view class="line"></view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		components: {  },
		data() {
			return {
				//类别
				categoryList: [ ],
				categoryCurrent: 0,
				// banner
				bannerList: [ ],
				
				//news
				newsList: [ ],
				
				//参数
				params:{ 
					pageNum: 1,
					pageSize: 10,
					bannerType: 2,
				},
				
				//图片域名
				imgDomain: this.$config.IMG_URL,
				
				//分类
				dictType: 'cms_news_category',
			}
		},
		onLoad() {
			this.loadData();
		},
		methods: {
			//加载数据
			async loadData(){
				//category
				const categoryRes = await this.$http({url: '/wechat/dict/type/' + this.dictType, data: this.params});
				this.categoryList = categoryRes.data;
				//banner
				const bannerRes = await this.$http({url: '/wechat/banner/list', data: this.params});
				this.bannerList = bannerRes.rows;
				//news
				const newsRes = await this.$http({url: '/wechat/news/list', data: this.params});
				this.newsList = newsRes.rows;
			},
			//改变类别
			changeCategory(item){
				this.categoryCurrent = item.index;
			},
			//进入详情
			clickItem(item){
				this.$u.route({ params: item, url: 'pages/news/detail' })
			},
			search(){
				
			},
			clear() {
				this.params.keywords = '';
			},
		},
	}
</script>

<style lang="scss">
	
.search-area{
	background-color: #FFFFFF;
	padding: 0 20rpx 10rpx 20rpx;
}	
	
.swiper-box {
	width: 100%;
	height: auto;
	box-sizing: border-box;
	padding: 10rpx 30rpx;
	background-color: white;
}

.new-list {
	margin-top: 2rpx;
	.item {
		padding: 40rpx 24rpx 0 24rpx;
		.info {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			padding-bottom: 4rpx;
			.text {
				flex-grow: 1;
				flex-shrink: 1;
				display: flex;
				flex-direction: column;
				margin-right: 40rpx;
				.title {
					flex-grow: 1;
					flex-shrink: 1;
					font-size: 30rpx;
					line-height: 1.5;
					display: -webkit-box;
					text-overflow: ellipsis;
					word-break: break-all;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					overflow: hidden;
					height: 72rpx;
					margin-bottom: 8rpx;
				}
				.other {
					flex-grow: 1;
					flex-shrink: 1;
					display: flex;
					align-items: center;
					font-size: 24rpx;
					color: #999;
					line-height: normal;
					.left {
						display: flex;
						flex-grow: 1;
						flex-shrink: 1;
						.type {
							display: -webkit-box;
							text-overflow: ellipsis;
							word-break: break-all;
							-webkit-line-clamp: 1;
							-webkit-box-orient: vertical;
							overflow: hidden;
							width: 100rpx;
							margin-right: 15rpx;
						}
					}
					.right {
						display: flex;
						flex-direction: row;
						justify-content: center;
						align-items: center;
						flex-grow: 0;
						flex-shrink: 0;
						margin-right: 5rpx;
						image {
							flex-grow: 1;
							flex-shrink: 1;
							width: 28rpx;
							height: 28rpx;
							margin-right: 12rpx;
						}
						text {
							flex-grow: 1;
							flex-shrink: 1;
						}
					}
					.view {
						image {
							width: 40rpx;
							height: 40rpx;
							margin-right: 5rpx;
						}
					}
				}
			}
			.photo {
				image {
					height: 170rpx;
					width: 222rpx;
					border-radius: 10rpx;
				}
			}
		}
		.line {
			display: bock;
			width: 100%;
			height: 1rpx;
			margin-top: 22rpx;
			background: #e8e8e8;
		}
		&:last-child {
			.line {
				//display: none;
			}
			//padding-bottom: 30rpx;
		}
	}
}
</style>
